// ScreenRecording.jsx
import React, { useState, useRef } from 'react';
import RecordRTC from 'recordrtc';
import './ScreenRecording.css'; // Import CSS file for styling

function ScreenRecording() {
  const [isRecording, setIsRecording] = useState(false);
  const [recordedVideoUrl, setRecordedVideoUrl] = useState('');
  const recorderRef = useRef(null);

  const startRecording = async () => {
    try {
      // Capture both screen video and audio
      const screenStream = await navigator.mediaDevices.getDisplayMedia({ video: true });
      const audioStream = await navigator.mediaDevices.getUserMedia({ audio: true });
      
      // Combine both streams into one
      const combinedStream = new MediaStream([...screenStream.getTracks(), ...audioStream.getTracks()]);
      
      const recorder = new RecordRTC(combinedStream, {
        type: 'video',
        mimeType: 'video/webm;codecs=vp8,opus', // Ensure compatibility
      });
      
      recorder.startRecording();
      recorderRef.current = recorder;
      setIsRecording(true);
    } catch (error) {
      console.error('Error starting recording:', error);
    }
  };

  const stopRecording = () => {
    const recorder = recorderRef.current;
    if (recorder) {
      recorder.stopRecording(() => {
        const blob = recorder.getBlob();
        const videoUrl = URL.createObjectURL(blob);
        setRecordedVideoUrl(videoUrl);
        setIsRecording(false);
      });
    }
  };

  const downloadVideo = () => {
    const anchor = document.createElement('a');
    anchor.href = recordedVideoUrl;
    anchor.download = 'recorded-video.webm';
    anchor.click();
    URL.revokeObjectURL(recordedVideoUrl);
  };

  return (
    <div className="container">
      <h1 className="title">SCREEN RECORDING</h1>
      <div className="button-group">
        <button className="start" onClick={startRecording} disabled={isRecording}>
          {isRecording ? 'Recording...' : 'Start'}
        </button>
        <button className="stop" onClick={stopRecording} disabled={!isRecording}>
          Stop
        </button>
        {recordedVideoUrl && (
          <button className="download" onClick={downloadVideo}>
            Download Video
          </button>
        )}
      </div>
    </div>
  );
}

export default ScreenRecording;
